import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        'API-KEY': '8389851f-540e-4430-b9f2-c897b55257fa'
       }
});

export const usersAPI = { 
getUsers(currentPage, pageSize){
    return instance.get(`users?page=${currentPage}&count=${pageSize}`)
    .then(response => {
        return response.data;
    });
},

setFollow(user) {
    if (user.followed === false) {
        return instance.post(`follow/${user.id}`, {})
            .then(response => response.data)
    } else {
        return instance.delete(`follow/${user.id}`, {})
            .then(response => response.data)
    }
},

getProfile(userId) {
    console.warn('Obsolete method. Please use profileAPI object.')
    return profileAPI.getProfile(userId)
}
};

export const profileAPI = {
getProfile(userId) {
    return instance.get(`profile/${userId}`)
        .then(response => {
            return response.data
        });
},

getStatus(userId) {
    return instance.get(`profile/status/${userId}`)
        .then(response => {
            return response.data
        });
    },

    updateStatus(status) {
        return instance.put('profile/status', { status: status })
            .then(response => {
                return response.data
            });
        }
}

export const authAPI = {
    me(){
        return instance.get(`auth/me`, {})
        .then(response => response.data)
    },
    login(email, password, rememberMe = false, error) {
        return instance.post(`auth/login`, {email, password, rememberMe, error})
        .then(response => response.data);
    },
    logout() {
        return instance.delete(`auth/login`, {})
        .then(response => response.data);
    }
}